import React, { Component } from 'react';
import './App.css';
import 'typeface-roboto';
import axios from 'axios';
import Table from '@material-ui/core/Table';

import AffichageCas from "./Affichage/AffichageCas";
import TableHead from "@material-ui/core/TableHead/TableHead";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody/TableBody";


class App extends Component {
  constructor(props) {
		super(props);

		this.state = {
			cas:[]
		}
	}


    componentDidMount() {
        console.log("--- GETTING DATA ---");

        // ce qui était prévu d'utiliser à la base, mais cela ne créait pas le state,
        // et je ne voyais pas pourquoi,
        // d'où l'utilisation d'axios pour faciliter le tout.
        // fetch('http://localhost:4000/cas/')

        axios.get('http://localhost:4000/cas/')
            .then(res => {
                this.setState({
                    cas: res.data
                });
                console.log("--- DATA RETRIEVED SUCCESSFULLY ---");
            })
            .catch((error) => {
                console.log(error);
            })
    }



    GetCasDatabaseDetails(){
        return this.state.cas.map((res, i) => {
            return <AffichageCas obj={res} key={i} />;
        });
    }



  addHobby() {
		let oldHobbies = this.state.hobbies;
		this.setState({
			hobbies: oldHobbies.concat(this.input.value)
    });

    this.input.value = "";
  }

  removeHobby(hobby) {
		/*
		let oldHobbies = this.state.hobbies;
		let pos = this.state.hobbies.indexOf(hobby);
		oldHobbies.splice(pos, 1);
		*/
	const oldHobbies = this.state.hobbies.filter(
      (elem, index) => {
        return (elem !== hobby) ? elem : null;
      }
    );

		this.setState({
			hobbies: oldHobbies
		});
	}
  getDataFromServer() {
    console.log("--- GETTING DATA ---");
     fetch('https://jsonplaceholder.typicode.com/todos')
     .then(response => {
       return response.json(); // transforme le json texte en objet js
     })
     .then(data => { // data c'est le texte json de response ci-dessus
       let newHobbies = [];
       data.forEach((el) => {
         newHobbies.push(el.title);
       });

       this.setState({
        hobbies: newHobbies
      });

     }).catch(err => {
       console.log("erreur dans le get : " + err)
     });

  }

  componentWillMount() {
    console.log("Component will mount");
    // on va chercher des donnees sur le Web avec fetch, comme
    // on a fait avec VueJS
    this.getDataFromServer();
  }

  render() {


    return (
      <div className="App">
          <div className="table-wrapper">
              <span>Cas recensés par le GEIPAN</span>


              <Table>
                  <TableHead>

                      <TableRow>
                          <TableCell>Id_cas</TableCell>
                          <TableCell>Nom du dossier</TableCell>
                          <TableCell>Lieu de l'incident</TableCell>
                          <TableCell>Résumé du cas</TableCell>
                          <TableCell>Date de la dernière mise à jour</TableCell>
                      </TableRow>
                  </TableHead>
                      <TableBody>
                          {this.GetCasDatabaseDetails()}
                      </TableBody>
              </Table>
          </div>);



      </div>





    );
  }
}

export default App;



